      /*zoom: 0.7952;*/
    /*}*/
  /*}*/
  /*不显示竖向Menu的边栏*/
  .ivu-menu-vertical.ivu-menu-light:after {
    width: 0px;
  }

  .leftPanel {
    height: 100%;
  }

  .rightPanel {
    display: flex;
    display: -webkit-flex;
    -webkit-box-orient: vertical;
    -webkit-flex-flow: column;
    flex-flow: column;
