<template>
  <!--类别相同，内容不同的页面，使用同一个vue，不同的router名称跳转到相同的vue，并通过router名称判断显示内容-->
  <Row class="fullWidth">
    <iCol span="1" class="leftPanel left-background">
      <Row style="padding: 7px;" class="left-top">
        <span style="font-size: 16px;">
          {{ currentOrganization.name }}
        </span>
        <!--<div v-if="!isHightest" style="margin-top: 10px;padding-left: 10px;" title="返回上级机构" class="cursor-pointer"-->
             <!--@click="returnUpperLevel"><img src="../../assets/return.png" style="height:15px;"/></div>-->
      </Row>
      <Menu :activeName="activeName" ref="companyMenu" width="auto" class="left-menu" :open-names="['company']" @on-select="onNaviMenuClick">
        <Submenu name="company">
          <template slot="title">
            <span>单位管理</span>
            <img src="../../assets/menu/up.png" class="menu-up" height="4" width="8"/>
            <img src="../../assets/menu/down.png" class="menu-down" height="4" width="8"/>
          </template>
          <Menu-item name="base_info">基本信息</Menu-item>
          <!--<Menu-item name="archival_data">档案资料</Menu-item>-->
        </Submenu>
        <Submenu name="organization">
          <template slot="title">
            <span>组织机构</span>
            <img src="../../assets/menu/up.png" class="menu-up" height="4" width="8"/>
            <img src="../../assets/menu/down.png" class="menu-down" height="4" width="8"/>
          </template>
          <Menu-item name="role">角色管理</Menu-item>
          <Menu-item name="department">部门管理</Menu-item>
          <Menu-item name="person">人员管理</Menu-item>
          <Menu-item name="sub_organization">下级单位</Menu-item>
        </Submenu>
        <Submenu name="building">
          <template slot="title">
            <span>建筑物管理</span>
            <img src="../../assets/menu/up.png" class="menu-up" height="4" width="8"/>
            <img src="../../assets/menu/down.png" class="menu-down" height="4" width="8"/>
          </template>
          <Menu-item name="building_list">建筑物</Menu-item>
          <Menu-item name="device">设备库</Menu-item>
        </Submenu>
      </Menu>
    </iCol>
    <iCol class="rightPanel">
      <router-view></router-view>
    </iCol>
  </Row>
</template>

<script>
  import config from '../../config/config';
  import niceScroll from 'jquery.nicescroll';
  import Vue from 'vue';
  import server from '../../config/api';


  export default {
    data() {
      return {
        activeName:'base_info',
        currentOrganization: {},
//        isHightest: true
      };
    },
    methods: {
      fetchData: function () {
        document.title = '单位管理 - ' + config.web_name;
        this.$store.commit('setActiveNaviMenu', 'company');
      },
      initData: function() {
        let orgId = undefined;
        let page = this.topYunSession('route_list');
        if(page === null || page.orgId === undefined) {
          // 根节点
          orgId = localStorage.current_org_id;
        } else {
          orgId = page.orgId;
        }
        // 初始化，通过currentOrgId查询机构数据作为根节点数据
        this.$http.get(server.api.organization.baseInfo.get.replace('{orgId}', orgId) + "?orgClass=NORMAL_ENTITY").then((response) => {
          this.currentOrganization = response.body.data;
        }, (error) => {
          this.$Message.error('单位管理基本信息获取失败!');
        });
      },
//      returnUpperLevel: function () {
//        // 返回上一级
//        let orgList = this.getYunSession('org_list');
//        orgList.splice(orgList.length-1, 1)[0];
//        this.setYunSession('org_list', orgList);
//        let currentOrgId = orgList[orgList.length-1];
//        if (orgList.length === 1) {
//          this.isHightest = true;
//          this.activeName="sub_organization";
//        }
//        // 重新查询机构数据
//        this.$http.get(server.api.organization.baseInfo.get.replace('{orgId}', currentOrgId) + "?orgClass=NORMAL_ENTITY").then((response) => {
//          this.currentOrganization = response.body.data;
//          this.$router.push({
//            path: '/supervision/sub_organization',
//          });
//        }, (error) => {
//          this.$Message.error('单位管理基本信息获取失败!');
//        });
//      },
      onNaviMenuClick: function (key) {
        this.$router.push({
          path: key
        });
      },
      setOrgInfo: function (page) {
        if(page === null) {
          // 一个消息总线，此时代表顶部menu按钮点击，重新请求数据
          this.clearYunSession('route_list');
          if(this.$route.path === '/company/base_info') {
            this.$router.go(0);
            return;
          }
          page = {
            router: '/supervision/base_info',
            query: {
              active_name: 'base_info'
            }
          };
        } else {
          let prePage = this.topYunSession('route_list');
          if(prePage === null) {
            // 此时当前页面为根节点
            this.clearYunSession('route_list');
            prePage = {};
          } else {
            // 下级机构进入，修改top中路径，指向sub_organization
            prePage = this.popYunSession('route_list');
          }
          prePage.router = {
            path: '/company/sub_organization',
            query: {
              active_name: 'sub_organization'
            }
          };
          this.pushYunSession('route_list', prePage);
          this.pushYunSession('route_list', page);
        }
        this.$router.replace(page.router);
      }
    },
    watch: {
      '$route': function(val, oldVal) {
        this.initData();
        if(this.$route.query !== undefined && this.$route.query.active_name !== undefined) {
          this.$nextTick(() => {
            this.$refs.companyMenu.currentActiveName = this.$route.query.active_name;
          });
        }
      }
    },
    created: function () {
      this.$root.eventHub.$on('setOrgInfo', this.setOrgInfo)
    },
    beforeDestroy: function () {
      this.$root.eventHub.$off('setOrgInfo', this.setOrgInfo)
    },
    mounted() {
      this.initData();
      this.fetchData();
    }
  }
</script>



